import { useOutletContext } from "@remix-run/react";
import hexagon from "~/components/clippaths/hexagon";
import type Wallet from "~/wallet";
import type { ImageNFT, SocialLink } from "~/wallet";

import { FaEthereum } from "@react-icons/all-files/fa/FaEthereum";

export default function Index() {
  const wallet = useOutletContext<Wallet>()
  return (
    <div className="flex flex-row">
      <div className="flex flex-col items-center justify-center border-r border-r-gray-100 p-8">
        <div style={{ clipPath: hexagon, width: 200, height: 188 }} className="mb-4">
          <img src={wallet.avatar} alt={'Wallet Avatar'} className="-translate-y-1.5"/>
        </div>
        <span className="text-lg font-bold">{wallet.ens}</span>
        <div className="flex items-center text-sm text-gray-400">
          <span className="mr-2">{wallet.address.slice(0, 5)}..{wallet.address.slice(-3)}</span>
          <FaEthereum />
          <span>{wallet.balance.toFixed(3)}</span>
        </div>
        <div className="flex gap-2 justify-center w-full mt-4">
          {wallet.socialLinks.map((link: SocialLink) => (
            <SocialButton key={link.url} accent={wallet.accent} link={link} />
          ))}
        </div>
      </div>
      <div className="flex flex-1 flex-col px-6 pt-6">
        <div className="grid grid-cols-3 gap-3">
          {wallet.featuredTokens.map((token) => (
            <ImageToken key={token.name} nft={token} accentHue={wallet.accentHue} />
          ))}
        </div>
      </div>
    </div>
  );
}

function SocialButton({ link, accent }: { link: SocialLink, accent: string }) {
  return (
    <a href={link.url} className={`p-3 h-12 w-12 flex items-center justify-center rounded-xl border border-white border-opacity-10`} style={{ backgroundColor: accent + "11", boxSizing: 'border-box' }}>
      <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox={link.icon.viewBox} className="text-2xl" style={{ color: accent }} height="1em" width="1em" xmlns="http://www.w3.org/2000/svg" dangerouslySetInnerHTML={{ __html: link.icon.body }}></svg>
    </a>
  );
}

function ImageToken({ nft, accentHue }: { nft: ImageNFT, accentHue: number }) {
  return (
    <div className="h-full w-full overflow-hidden rounded-2xl relative group z-30 cursor-pointer">
      <img src={nft.image} alt={nft.name} className="object-fill w-full" />
      <div className="absolute top-0 left-0 p-2 h-full w-full z-20 flex flex-col justify-end translate-y-1 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all pointer-events-none" style={{ background: `hsla(${accentHue}, 30%, 40%, 80%)` }}>
        <span className="font-bold text-sm text-ellipsis text-white whitespace-nowrap overflow-hidden mb-0.5 ml-0.5 translate-y-0.5 group-hover:translate-y-0 transition-transform">{nft.name}</span>
        <div className="flex items-center">
          <img src={nft.collection.image} alt={nft.collection.name} className="h-6 w-6 rounded-full mr-1" />
          <span className="font-bold text-xs text-ellipsis text-white opacity-80 whitespace-nowrap overflow-hidden">{nft.collection.name}</span>
        </div>
      </div>
    </div>
  )
}